.IcingaAlert {
  border: 10px solid #bfc1c3;
  color: #0b0c0c;
  display: block;
  padding: 10px;
  text-decoration: none; }

.IcingaAlert__header {
  text-align: center;
  margin: 0 0 10px; }

.IcingaAlert__counts {
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0; }

.IcingaAlert__count {
  -ms-flex: 1 0 33%;
      flex: 1 0 33%;
  text-align: center; }
  .IcingaAlert__count strong {
    display: block;
    font-size: 200%; }

.IcingaAlert--critical {
  border-color: #b10e1e; }

.IcingaAlert--warning {
  border-color: #f47738; }

.IcingaAlert--clear {
  border-color: #006435; }

.IcingaAlert--unknown {
  border-color: #912b88; }



